.App {
  font-family: sans-serif;
  text-align: center;
}
* {
  margin: 0;
  padding: 0;
  font-family: "sans-serif";
}

body {
  background: #fcfcfc;
}
.container {
  width: 100%;
  height: 100vh;
}
.dropdown {
  width: 400px;
  user-select: none;
  margin: 100px auto;
  position: relative;
}
.dropdown .dropdown-btn {
  padding: 10px;
  background: #fff;
}
.dropdown.dropdown-btn {
  padding: 15px 20px;
  background: #fff;
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 06);
  font-weight: bold;
  color: #333;
  display: flex;
  align-items: contex;
  cursor: pointer;
  justify-content: space-between;
}

.dropdown .dropdown-content {
  position: absolute;
  top: 110%;
  left: 0;
  padding: 10px;
  background: #fff;
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 06);
  font-weight: 500;
  color: #333;
  width: 95%;
}
.dropdown .dropdown-content .dropdown-item {
  padding: 10px;
  cursor: pointer;
  transition: all 0.2s;
}
.dropdown .dropdown-content .dropdown-item:hover {
  background: #c4c2c2;
}
